import React, { useState } from 'react'
import styles from './cookies.module.scss'
import CookiesLib from 'js-cookie'

const hideBanner = CookiesLib.get('hideBanner')

const Cookies = () => {
  const [isBanner, set] = useState(hideBanner ? false : true)
  const hideCookiebanner = () => {
    set(false)
    CookiesLib.set('hideBanner', '1')
  }

  const banner = (
    <div className={styles.cookiebanner} id="cookiebanner">
      <div className="is-flex has-full-height">
      <p className={`is-centered is-hidden-mobile ${styles.cookietext}`}>This site uses cookies to learn what can be improved to provide you with a great experience. ðŸ“ˆðŸŽ‰ 
        <br />If you continue surfing the site, you agree to the <a href="#" className="is-underlined">use of cookies</a>. <button onClick={hideCookiebanner} href="#" className={`is-underlined ${styles.Iagree}`}>I agree</button>.
      </p>
      <p className={`is-centered is-hidden-tablet ${styles.cookietext}`}>This site uses cookies to learn and provide you with a great experience. ðŸ“ˆðŸŽ‰ 
        <br />If you continue surfing the site, you agree to the <a href="#" className="is-underlined">use of cookies</a>. <button onClick={hideCookiebanner} href="#" className={`is-underlined ${styles.Iagree}`}>I agree</button>.
      </p>
      
      <button onClick={hideCookiebanner} className={`button ${styles.cookiebutton}`}><i className="fas fa-times"></i></button>
      </div>
    </div>
  )

  return isBanner ? banner : null
}

export default Cookies